import Main from "@/views/homePage/components/main";
export default {
  data: function data() {
    return {
      setting: null,
      loading: false,
      code: {
        header: [],
        footer: []
      }
    };
  },
  components: {
    Main: Main
  },
  watch: {},
  created: function created() {
    this.setting = JSON.parse(JSON.stringify(this.$store.state.configJson));
    this.getInfo();
  },
  mounted: function mounted() {
    if (document.getElementById("loading")) document.getElementById("loading").remove();
  },
  methods: {
    getInfo: function getInfo() {
      var _this = this;

      // location.host.split('.').length < 3 || 
      // location.host.split('.')[0].toLowerCase() == 'www' || 去除 贴牌逻辑修改
      if (location.host.split('.')[0].toLowerCase() == 'sandboxwww' || location.host.split('.')[0].toLowerCase() == 'supplier' || location.host.split('.')[0].toLowerCase() == 'sandboxsupplier' && location.host.indexOf('localhost') < 0) {
        return;
      }

      this.loading = true; // location.host

      var url = location.host;

      if (location.host.indexOf('localhost') > -1) {
        //测试
        url = 'shop242.myourmall.com';
      }

      this.$apiCall('api.User.getInfoFromUrl', {
        url: url
      }, function (r) {
        if (r.ErrorCode == 9999) {
          localStorage.setItem('c_apiShopId', r.Data.Results.shopId);

          _this.getItem();
        } else {
          _this.loading = false;

          _this.$elementMessage(r.Message, "error");
        }
      });
    },
    getItem: function getItem() {
      var _this2 = this;

      this.$apiCall("api.VendorShop.getShopConfig", {}, function (r) {
        _this2.loading = false;

        if (r.ErrorCode == 9999) {
          if (r.Data.Results.length !== 0) {
            var shopConfig = r.Data.Results;

            if (shopConfig.sort && shopConfig.sort.length) {
              shopConfig.sort.forEach(function (item) {
                if (item.type == "hotProduct") {
                  //热门商品列表
                  _this2.$apiCall("api.VendorShop.vendorFindProducts", {
                    categoryId: item.categoryItem ? item.categoryItem.id : "",
                    status: 1,
                    page: 1,
                    rowsPerPage: 16
                  }, function (r) {
                    if (r.ErrorCode == 9999) {
                      var arr = [];
                      r.Data.Results.forEach(function (e) {
                        if (e.status == 1) {
                          arr.push(e);
                        }
                      });

                      _this2.$set(item, "productItems", arr); // item.productItems = arr;

                    }
                  });
                } else if (item.type == "singleProduct") {
                  //单个商品
                  _this2.$apiCall("api.VendorShop.vendorFindProducts", {
                    productId: item.id ? item.id : '',
                    page: 1,
                    rowsPerPage: 10
                  }, function (r) {
                    if (r.ErrorCode == 9999) {
                      var data = r.Data.Results; // item.imgUrl = data[0].imgUrl;
                      // item.cost = data[0].cost;
                      // item.name = data[0].name;

                      _this2.$set(item, "imgUrl", data[0].imgUrl);

                      _this2.$set(item, "cost", data[0].cost);

                      _this2.$set(item, "name", data[0].name);
                    }
                  });
                } else if (item.type == "productCategory") {
                  _this2.$apiCall("api.ProductCategory.findByVendorRelateCategory", {
                    level: 1
                  }, function (r) {
                    if (r.ErrorCode == 9999) {
                      var data = r.Data.Results;
                      var items = [];

                      if (item.categorys && item.categorys.length && data.length) {
                        item.categorys.forEach(function (category, i) {
                          data.forEach(function (obj) {
                            if (category.id == obj.id) {
                              items.push(obj);
                            }
                          });
                        }); // item.categorys = items;

                        _this2.$set(item, "categorys", items);
                      }
                    }
                  });
                } else {}
              });
            }

            _this2.setting = shopConfig;
            localStorage.setItem("c_shopConfigJson", JSON.stringify(shopConfig));

            _this2.$store.commit('getStoreComponent', shopConfig);

            _this2.handleCode(shopConfig.customCode);
          } else {
            _this2.setting = JSON.parse(JSON.stringify(_this2.$store.state.configJson));
          }
        } else {
          _this2.$message({
            message: r.Message,
            type: "error"
          });
        }
      });
    },
    handleCode: function handleCode(arr) {
      var _this3 = this;

      this.code.footer = [];
      this.code.header = [];
      arr.forEach(function (item) {
        if (item.pages.includes('首页')) {
          if (item.position == '底部') {
            _this3.code.footer.push(item.desc);
          }

          if (item.position == '顶部') {
            _this3.code.header.push(item.desc);
          }
        }
      });
    }
  }
};