export default {
  props: ['data'],
  data: function data() {
    return {};
  },
  methods: {
    import_pro: function import_pro(item) {
      var _this = this;

      this.$showLoading();
      this.$apiCall("api.Product.addFromVendorShopProduct", {
        id: item.id
      }, function (r) {
        if (r.ErrorCode == 9999) {
          _this.$message.success("Import Success !");
        } else {
          _this.$message.error(r.Message);
        }

        _this.$hideLoading();
      });
    }
  },
  components: {}
};