import DHeader from '@/views/homePage/components/header';
import Footer from '@/views/homePage/components/footer';
import demoBody from '@/views/homePage/components/body';
export default {
  props: ['setting', 'code'],
  components: {
    DHeader: DHeader,
    Footer: Footer,
    demoBody: demoBody
  },
  data: function data() {
    return {
      headerMenu: []
    };
  },
  watch: {
    'setting.header.activeMenu': {
      handler: function handler() {
        this.getMenu();
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    getMenu: function getMenu() {
      var _this = this;

      if (this.setting.header.activeMenu) {
        //有启用头部
        this.setting.menu.forEach(function (item) {
          if (item.id == _this.setting.header.activeMenu) {
            _this.headerMenu = item;
          }
        });
      }
    }
  }
};