export default {
  props: ['data'],
  data: function data() {
    return {
      key: 1,
      active: 0
    };
  },
  watch: {
    'data.images': function dataImages() {
      this.key = new Date().getTime();
    }
  },
  computed: {},
  mounted: function mounted() {},
  created: function created() {},
  methods: {
    sliderChange: function sliderChange(index) {
      this.active = index;
    },
    getHeight: function getHeight() {
      var _this = this;

      //自适应
      var h = 0;
      this.$dict.storeSliderSizeArr.forEach(function (e) {
        if (e.value == _this.data.size) {
          h = e.h;
        }
      });

      if (!h) {
        var height = (document.getElementById('demoBody') ? document.getElementById('demoBody').clientWidth : 1920) / this.data.images[this.active].width * this.data.images[this.active].height;
        h = height > 0 ? height : 600;
      }

      return h;
    }
  }
};